// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import App from "./App";
import router from "./router";
import Vuex from 'vuex';
import 'es6-promise/auto';
import store from './store'


Vue.use(Vuex);

// alert 
import VueSweetalert2 from "vue-sweetalert2";
import "sweetalert2/dist/sweetalert2.min.css";
// slick 
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'

// Loading 
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";

// type 
import VueTypedJs from 'vue-typed-js'

Vue.use(VueTypedJs);
Vue.use(VueSweetalert2);
Vue.use(VueAxios, axios);
Vue.component("VueSlickCarousel", VueSlickCarousel);
Vue.component("Loading", Loading);

import AOS from 'aos'
import 'aos/dist/aos.css'
Vue.prototype.aos = AOS;

axios.defaults.withCredentials = true;
Vue.config.productionTip = false;


router.beforeEach(async(to, from, next) => {
  
  // to: Route:  
  // from: Route: 
  // next: Function:  resolve  next 

  // routelogin
  const route = ['admin'];
  const apiUrl = `${process.env.APIPATH}/api/login`;
    await axios.get(apiUrl).then(async(response) => {
        if (route.indexOf(to.path.split('/')[1]) >= 0){
            console.log("VUEXlogin", response.data);
          if (!response.data.success){
              console.log('');
              await router.push('/login');
            }
        }
          // loginhome 
          if(response.data.success && to.name === 'Login'){
            await router.push({ path: '/admin/center', });
          }
        
      });
      
    ;
  next();
});


/* eslint-disable no-new */
new Vue({
  el: "#app",
  router,
  store,
  components: {
    App
  },
  template: "<App/>"
});



