<template>
  <div class="leftSide">
    <ul class="memberList">
      <li class="mli memberCenter active"></li>
      <li class="mli memberInfo">
        
        <ul class="memberInfoList">
          <li class="memberModify">
            <router-link to="/admin/membermodify"></router-link>
          </li>
          <li class="memberModifyPassword">
            <router-link to="/admin/membermodifypassword"></router-link>
          </li>
        </ul>
      </li>

      <li class="mli orderList">
        
        <ul class="memberEachOrder">
          <li class="myGivesOrder">
            <router-link to="/admin/giveorder"></router-link>
          </li>
          <li class="myGetOrder">
            <router-link to="/admin/getorder"></router-link>
          </li>
        </ul>
      </li>
      <li class="mli wishList">
        <router-link to="/admin/wishlist"></router-link>
      </li>
      <li class="mli memberShelves">
        <router-link to="/admin/givesshelves"></router-link>
      </li>
      <li class="mli logOut" @click="signout"></li>
    </ul>
  </div>
</template>

<script>
const auth = firebase.auth();

export default {
  data() {
    return {};
  },
  methods: {
    signout() {
      console.log("");
      auth.signOut().then(() => {
        this.axios.post(`${process.env.APIPATH}/api/logout`).then(response => {
          setTimeout(() => {}, 1000);
          console.log(response.data);
          if (response.data.success) {
            this.$swal("");
            this.$router.push("/home");
          }
        });
      });
    }
  }
};
</script>

<style scoped>
.mli a {
  color: #4c4c4c;
}
.mli a:hover {
  color: #000;
}
.mli > ul > li a {
  color: #4c4c4c;
}
.mli > ul > li a:hover {
  color: #000;
}
</style>



