import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

import Login from '@/components/pages/Login'; //
import Dashboard from '@/components/pages/Dashboard'; //
import center from '@/components/pages/center'; //
import Signup from '@/components/pages/Signup'; //
import Home from '@/components/pages/Home'; //
import MemberCenter from '@/components/pages/MemberCenter'; //
import GetOrderDetail from '@/components/pages/GetOrderDetail'; //
import GiveOrderDetail from '@/components/pages/GiveOrderDetail'; //
import CheckForm from '@/components/pages/CheckForm'; //
import GetOrder from '@/components/pages/GetOrder'; //
import GiveOrder from '@/components/pages/GiveOrder'; //
import GivesList from '@/components/pages/GivesList'; //
import ForgetPassword from '@/components/pages/ForgetPassword'; //
import MemberModify from '@/components/pages/MemberModify'; //
import MemberModifyPassword from '@/components/pages/MemberModifyPassword'; //
import GivesDetail from '@/components/pages/GivesDetail'; //
import GivesShelves from '@/components/pages/GivesShelves'; //
import GivesShelvesForm from '@/components/pages/GivesShelvesForm'; //
import GivesInfo from '@/components/pages/GivesInfo'; //
import WishList from '@/components/pages/WishList'; //

import store from '../store';


Vue.use(Router);

export default new Router({


  routes: [

    {
      path: '*', //
      redirect: 'home',
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      // meta: {
      //   requiresAuth: true
      // }
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,

    },
    {
      path: '/admin',
      name: 'Dashboard',
      component: Dashboard,
      children: [{
          path: 'center',
          name: 'center',
          component: center,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'membercenter',
          name: 'MemberCenter',
          component: MemberCenter,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'getorderdetail/:goods_id',
          name: 'GetOrderDetail',
          component: GetOrderDetail,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'giveorderdetail/:goods_id',
          name: 'GiveOrderDetail',
          component: GiveOrderDetail,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'getorder',
          name: 'GetOrder',
          component: GetOrder,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'giveorder',
          name: 'GiveOrder',
          component: GiveOrder,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'membermodify',
          name: 'MemberModify',
          component: MemberModify,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'membermodifypassword',
          name: 'MemberModifyPassword',
          component: MemberModifyPassword,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'givesshelves',
          name: 'GivesShelves',
          component: GivesShelves,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'givesshelvesform',
          name: 'GivesShelvesForm',
          component: GivesShelvesForm,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'givesshelvesform/:goods_id',
          name: 'GivesShelvesForm',
          component: GivesShelvesForm,
          meta: {
            requiresAuth: true
          }
        },

        {
          path: 'givesdetail/:goods_id',
          name: 'GivesDetail',
          component: GivesDetail,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'wishlist',
          name: 'WishList',
          component: WishList,
          meta: {
            requiresAuth: true
          }
        }
      ],
    },
    {
      path: '/signup',
      name: 'Signup',
      component: Signup,
    },
    {
      path: '/checkform/:goods_id',
      name: 'CheckForm',
      component: CheckForm,
    },
    {
      path: '/giveslist',
      name: 'GivesList',
      component: GivesList,

    },
    {
      path: '/givesinfo/:goods_id',
      name: 'GivesInfo',
      component: GivesInfo,

    },
    {
      path: '/forgetpassword',
      name: 'ForgetPassword',
      component: ForgetPassword,
    },


  ]
})



